import "./NewsCard.scss";
import React, { useRef, useEffect } from "react";
import { NewsFeedDataType } from "../News";
import { URL_IMAGE } from "utils/constants";
import { clampText } from "utils";
import LazyImage from "libs/lazy-load/LazyImage";
import StyledButton from "components/styled-button/StyledButton";
import gsap from "gsap";

export interface NewsCardProps {}

const NewsCard: React.FC<NewsCardProps & NewsFeedDataType> = (props) => {
  const date = `${new Date(props.date).toLocaleString("default", { month: "long" })} ${new Date(props.date).getDate()}, ${new Date(props.date).getFullYear()}`;
  const cardRef = useRef(null);

  useEffect(() => {
    gsap.fromTo(
      cardRef.current,
      {
        y: 100,
      },
      {
        y: 0,
      }
    );
  }, []);

  return (
    <div className="com-news-card" ref={cardRef}>
      <img src={`${URL_IMAGE}/facebook.svg`} alt="" className="com-news-card__facebook" />
      <img src={`${URL_IMAGE}/ribbon.svg`} alt="" className="com-news-card__ribbon" />
      <LazyImage src={props.images[0]} alt="" className="com-news-card__image" />
      <div className="com-news-card__date">{date}</div>
      <div className="com-news-card__description">{clampText(props.description, 200)}</div>

      <a href={props.link} target="_blank" rel="noopener noreferrer" className="com-news-card__link">
        <StyledButton className="com-news-card__read-post">READ POST</StyledButton>
      </a>
    </div>
  );
};

export default NewsCard;
