import "./StaticTextPage.scss";
import React from "react";
import { joinNames } from "utils";

export interface StaticTextPageDataType {
  title: string;
  description: string;
  background?: string;
}

export interface StaticTextPageProps {
  data: StaticTextPageDataType;
  classes?: {
    root?: string;
    title?: string;
    description?: string;
  };
}

const StaticTextPage: React.FC<StaticTextPageProps> = ({ classes, data }) => {
  return (
    <div className={joinNames(["com-static-text-page", classes?.root])}>
      <div className={joinNames(["com-static-text-page__title", classes?.title])} dangerouslySetInnerHTML={{ __html: data.title.replace(/&nbsp;/g, " ") }}></div>
      <div className={joinNames(["com-static-text__description", classes?.description])} dangerouslySetInnerHTML={{ __html: data.description.replace(/&nbsp;/g, " ") }}></div>
    </div>
  );
};

export default StaticTextPage;
