import "./Footer.scss";
import React from "react";
import { useLocalize } from "store/actions/localizeAction";
import { ImageType } from "utils/contentType";
import { Link } from "react-router-dom";

export interface FooterProps {}
export interface FooterDataType {
  image: {
    iconGameloft: ImageType;
    iconFacebook: ImageType;
    iconDiscord: ImageType;
  };
  link: {
    facebook: string;
    discord: string;
  };
}
const Footer: React.FC<FooterProps> = (props) => {
  const data: FooterDataType = useLocalize("home.footer");
  return (
    <div className="com-footer">
      <div className="com-footer__image">
        <figure className="com-footer__image--logo">
          <a href="https://www.gameloft.com/en/" target="_blank" rel="noopener noreferrer">
            <img src={data?.image?.iconGameloft?.url} alt="iconGameloft" />
          </a>
        </figure>
        <figure className="com-footer__image--link">
          <a href={data?.link.facebook} target="_blank" rel="noopener noreferrer">
            <img className="mg-r1" src={data?.image?.iconFacebook?.url} alt="iconFb" />
          </a>
          <a href={data?.link.discord} target="_blank" rel="noopener noreferrer">
            <img className="mg-l05 mg-r05" src={data?.image?.iconDiscord?.url} alt="iconGame" />
          </a>
        </figure>
      </div>

      <ul>
        <li>
          <a href="https://www.gameloft.com/en/information/event" target="_blank" rel="noopener noreferrer">
            Contact Us
          </a>
        </li>
        <li>
          <a href="https://www.gameloft.com/en/privacy-notice" rel="noopener noreferrer" target="_blank">
            Privacy Policy
          </a>
        </li>
        <li>
          <a href="https://www.gameloft.com/en/conditions-of-use" target="_blank" rel="noopener noreferrer">
            Terms And Conditions
          </a>
        </li>
        <li>
          <a href="https://www.gameloft.com/en/legal/showcase-cookie-policy" target="_blank" rel="noopener noreferrer">
            Cookies Policy
          </a>
        </li>
        {/* //TODO : This page re-using data of header component, pls wait for update API for header */}
        <li>
          <Link to="/legal" target="_blank" rel="noopener noreferrer">
            Legal Notices
          </Link>
        </li>
        <li>
          <a href="https://www.gameloft.com/en/eula" target="_blank" rel="noopener noreferrer">
            EULA
          </a>
        </li>
      </ul>
      <p> 2020 Gameloft. All Rights Reserved. Gameloft And The Logo Gameloft Are Trademarks Of Gameloft In The Us And/or Other Countries.</p>
      <p>
        This site is protected by reCAPTCHA and the Google
        <a href="https://policies.google.com/privacy" target="_blank" rel="noopener noreferrer">
          Privacy Policy
        </a>
        and
        <a href="https://policies.google.com/terms" target="_blank" rel="noopener noreferrer">
          Terms of Service
        </a>
        apply.
      </p>
    </div>
  );
};

export default Footer;
