import get from "lodash-es/get";
import { ViewsData } from "mock-data/mock-all";
import { useDispatch, useSelector, useStore } from "react-redux";
import { ThunkAction } from "redux-thunk";
import { dispatch, IBaseAction, StoreType } from "store";
import { LocalizeAction } from "../reducers/localizeReducer";
import { axios } from "api";

// action type define

export interface LocalizeAction_SetData extends IBaseAction {
  type: LocalizeAction.SET_DATA;
  payload: {
    storePath: string;
    lang: string;
    data: any;
  };
}
export interface LocalizeAction_ChangeLanguage extends IBaseAction {
  type: LocalizeAction.CHANGE_LANGUAGE;
  payload: {
    language: string;
  };
}
export interface LocalizeAction_ChangeLanguage extends IBaseAction {
  type: LocalizeAction.CHANGE_LANGUAGE;
  payload: {
    language: string;
  };
}

export interface LocalizeAction_Invalidate extends IBaseAction {
  type: LocalizeAction.INVALIDATE;
  payload: {
    storePath: string;
  };
}

export type LocalizeActionType = LocalizeAction_SetData | LocalizeAction_ChangeLanguage | LocalizeAction_Invalidate; // | any other action;

// action creator

export const localizeAction_changeLanguage = (language: string): LocalizeAction_ChangeLanguage => {
  return {
    type: LocalizeAction.CHANGE_LANGUAGE,
    payload: {
      language,
    },
  };
};

export const localizeAction_setData = (storePath: string, lang: string, apiPath: string): ThunkAction<void, StoreType, undefined, LocalizeActionType> => async (dispatch) => {
  const data = await getLocalizeData(storePath, apiPath);

  if (data?.error?.errorType !== undefined) return; // discard incase error return from server

  dispatch({
    type: LocalizeAction.SET_DATA,
    payload: {
      storePath,
      lang,
      data,
    },
  });
};

export const localizeAction_Invalidate = (storePath: string): LocalizeAction_Invalidate => {
  return {
    type: LocalizeAction.INVALIDATE,
    payload: {
      storePath,
    },
  };
};
//////////////////////////// helper api ///////////////////////////////
// const supportedContents: string[] = [];
const getLocalizeData = async (storePath: string, query: string) => {
  if (query.includes("query")) {
    const response = await axios.post("/graphql", { query });
    let dataPath = storePath.split(".");
    return get(response.data.data, dataPath);
  } else {
    return mockData(query);
  }
};
const mockData = (view: string) => {
  return (ViewsData as any)[view];
};
//////////////////////////// helper hooks //////////////////////////////

export const useLanguage = (language?: string) => {
  const store = useStore<StoreType>().getState();
  const dispatch = useDispatch();
  if (!language) {
    const saveLang = localStorage.getItem("language");
    language = saveLang ? saveLang : "en";
  }

  if (language && store.localize.language !== language) {
    // save to local
    localStorage.setItem("language", language);
    // dispatch change
    dispatch(localizeAction_changeLanguage(language));
  }
};

export const useLocalize = <T = any>(storePath: string, apiPath: string = ""): T => {
  const absPath = storePath.startsWith(".");
  const path = absPath ? storePath.substr(1) : storePath;

  // select from store
  const store = useStore<StoreType>().getState().localize;
  const lang = store.language;
  const resultData: T = useSelector<StoreType, any>((store) => {
    return absPath ? get(store.localize.data, path) : get(store.localize.active, path);
  });

  const dispatch = useDispatch();

  if (!resultData && apiPath) {
    dispatch(localizeAction_setData(storePath, lang, apiPath));
  }

  return resultData;
};

export const localizeInvalidate = (storePath: string) => {
  dispatch(localizeAction_Invalidate(storePath));
};
