import "./DatePicker.scss";
import React from "react";
import { createPortal } from "react-dom";
import ReactDatePicker, { ReactDatePickerProps } from "react-datepicker";

// Custom for React Datepicker

export interface DatePickerProps extends ReactDatePickerProps {
  name?: string;
  placeholder?: string;
  classes?: {
    root?: string;
    input?: string;
  };
}

const DatePicker = React.forwardRef<any, React.PropsWithChildren<DatePickerProps>>((props, ref) => {
  const { name, placeholder, classes, ...others } = props;

  return (
    <ReactDatePicker
      {...others}
      className={classes?.root}
      popperContainer={DatepickerPortal}
      customInput={<DatepickerInput dname={name} dplaceholder={placeholder || props.placeholderText} dref={ref} dclassName={classes?.input} />}
    ></ReactDatePicker>
  );
});

export default DatePicker;

interface DatepickerInputProps {
  value?: string;
  onClick?: any;
  dclassName?: string;
  dplaceholder?: string;
  dname?: string;
  dref?: React.Ref<HTMLInputElement>;
}

const DatepickerInput = React.forwardRef<any, React.PropsWithChildren<DatepickerInputProps>>((props, ref) => {
  return (
    <input
      type="text"
      readOnly
      value={props.value}
      onClick={props.onClick}
      ref={props.dref}
      className={props.dclassName || ""}
      placeholder={props.dplaceholder}
      name={props.dname}
    />
  );
});

// work around with modal datepicker on mobile
const DatepickerPortal: React.FC = (props) => {
  return createPortal(props.children, document.getElementById("root") as HTMLElement);
};
