import "./FounderPack.scss";
import React from "react";
import { useLocalize } from "store/actions/localizeAction";
import StyledButton from "components/styled-button/StyledButton";
import LazyImage from "libs/lazy-load/LazyImage";
import { ImageType } from "utils/contentType";

export interface FounderPackProps {}
export interface FounderPackDataType {
  title: string;
  description: string;
  characterImg: ImageType;
  characterMobile: ImageType;
  backgroundImg: ImageType;
}

const onSignUp = () => {
  console.log("on Download");
};

const FounderPack: React.FC<FounderPackProps> = (props) => {
  const data: FounderPackDataType = useLocalize("home.founderPack");
  return (
    <div className="com-founder-pack" id="founder">
      <LazyImage className="bg-image" src={data?.backgroundImg?.url} alt="bg-image" />
      <div className="com-founder-pack__title">{data?.title}</div>
      <div className="com-founder-pack__description">{data?.description}</div>
      <figure>
        <LazyImage className="img-desktop" src={data?.characterImg?.url} alt="characterDesk" />
        <LazyImage className="img-mobile" src={data?.characterMobile?.url} alt="characterMobile" />
      </figure>
      <StyledButton className="com-founder-pack__btnDownload" onClick={onSignUp}>
        DOWNLOAD
      </StyledButton>
    </div>
  );
};

export default FounderPack;
