import React from "react";
import "./App.scss";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { useLanguage, useLocalize } from "store/actions/localizeAction";
import { ScrollToTop } from "libs/utils";
import Home from "../views/home/Home";
import Legal from "components/views/legal/Legal";
import { RECAPTCHA_KEY } from "utils/constants";
import useReCaptcha3 from "libs/recaptcha/ReCaptchaV3";
import { GET_HOME } from "utils/queryData";

const App: React.FC = () => {
  useReCaptcha3(RECAPTCHA_KEY);
  useLanguage(); // load save/default language
  useLocalize("home", GET_HOME);

  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="com-app">
        <ScrollToTop />
        <Switch>
          <Route path="/" exact>
            <Home></Home>
          </Route>
          <Route path="/legal" exact>
            <Legal></Legal>
          </Route>
        </Switch>
      </div>
    </Router>
  );
};

export default App;
