import React, { useState, useCallback, useEffect } from "react";
import "./Modal.scss";
import { createPortal } from "react-dom";

export interface ModalProps {
  rootElm?: string;
  isOpen: boolean;
  onBackClick?: Function;
  portal?: boolean;
  classes?: {
    root?: string;
    mask?: string;
    content?: string;
  };
}

const Modal: React.FC<ModalProps & React.HTMLAttributes<HTMLDivElement>> = (props) => {
  const root = props.rootElm || "root";
  const { classes, isOpen, onBackClick, portal = true, ...others } = props;

  useEffect(() => {
    if (isOpen) document.body.classList.add("modal-open");
    else document.body.classList.remove("modal-open");

    return () => {
      document.body.classList.remove("modal-open");
    };
  }, [isOpen]);

  const maskOnClick = () => {
    if (onBackClick) {
      onBackClick();
    }
  };

  const modalContent = (
    <div className={`modal${classes?.root ? ` ${classes.root}` : ""}`} {...others}>
      <div className={`modal__mask${classes?.mask ? ` ${classes.mask}` : ""}`} onClick={maskOnClick}></div>
      <div className={`modal__content${classes?.content ? ` ${classes.content}` : ""}`}>{props.children}</div>
    </div>
  );

  return !isOpen ? null : portal ? createPortal(modalContent, document.getElementById(root) as HTMLElement) : modalContent;
};

export { Modal };

type ModalWrapperType = Omit<ModalProps, "isOpen">;

const useModal = (): [React.FC<ModalWrapperType>, (open: boolean) => void] => {
  const [isOpen, openModal] = useState(false);

  const setOpen = useCallback(
    (open: boolean) => {
      openModal(open);
    },
    [openModal]
  );

  const ModalWrapper: React.FC<ModalWrapperType> = (props) => (
    <Modal isOpen={isOpen} rootElm={props.rootElm} onBackClick={props.onBackClick}>
      {props.children}
    </Modal>
  );

  return [ModalWrapper, setOpen];
};

export default useModal;

export interface ModalBackButtonProps {
  className?: string;
  onClick?: () => void;
}

export const ModalBackButton: React.FC<ModalBackButtonProps> = (props) => {
  return (
    <div onClick={() => props.onClick?.()} className={`modal__back-button ${props.className || ""}`}>
      {props.children}
    </div>
  );
};
