import arrowLeft from "assets/images/allies/arrow-left.png";
import arrowRight from "assets/images/allies/arrow-right.png";
import Carousel, { CarouselButtons, CarouselDots } from "libs/carousel/Carousel";
import { isEmpty, chunk } from "lodash-es";
import React, { useCallback, useRef, useState } from "react";
import { useLocalize } from "store/actions/localizeAction";
import "./Art.scss";
import LazyImage from "libs/lazy-load/LazyImage";
import { URL_IMAGE } from "utils/constants";
import { ImageType } from "utils/contentType";
export interface ArtProps {}
export interface ArtDataType {
  title: string;
  description: string;
  background: ImageType;
  images: ImageType[];
}
const Art: React.FC<ArtProps> = (props) => {
  const data: ArtDataType = useLocalize("home.artSurvival");
  const carousel = useRef<Carousel>();
  const [, setSlideState] = useState({ totalSlides: 0, selectedSlide: 0 });
  const iconDownload = `${URL_IMAGE}/icon-download.svg`;
  const setCarousel = useCallback((ref?: Carousel) => {
    carousel.current = ref;
    if (carousel.current) {
      setSlideState({ totalSlides: carousel.current.scrollSnapList().length, selectedSlide: carousel.current.selectedScrollSnap() });
    }
  }, []);

  const downloadImg = (url: string) => {
    let filename = url.substring(url.lastIndexOf("/") + 1);

    fetch(url, { cache: "no-cache" })
      .then((response) => response.blob())
      .then((blob) => {
        var reader = new FileReader();

        reader.onloadend = function () {
          var base64 = reader.result!;
          var link = document.createElement("a");
          link.setAttribute("href", base64?.toString());
          link.setAttribute("download", filename);
          link.click();
        };
        reader.readAsDataURL(blob);
      });
  };

  return (
    <div className="com-art" id="art">
      <LazyImage className="bg-image" src={data?.background?.url} alt="bg-image" />

      <div className="com-art__title">{data?.title}</div>
      <div className="com-art__description">{data?.description}</div>

      {!isEmpty(data) && (
        <div className="carousel-art">
          <Carousel
            carouselRef={(ref) => setCarousel(ref)}
            options={{
              draggableClass: "carousel-draggable",
              draggingClass: "carousel-dragging",
              align: "start",
            }}
          >
            {chunk(data?.images, 3).map((item, index: number) => {
              return (
                <div className="com-art__card" key={index}>
                  {item.map((item, index1: number) => {
                    return (
                      <figure key={index1} style={{ position: "relative" }}>
                        <LazyImage className="bg-mobile" src={item?.url} alt="img-arts" />
                        <LazyImage
                          className="download"
                          src={iconDownload}
                          alt="icon-download"
                          onClick={() => {
                            downloadImg(item?.url);
                          }}
                        />
                      </figure>
                    );
                  })}
                  )
                </div>
              );
            })}
          </Carousel>

          <div className="carousel-dots-container">
            <CarouselDots carousel={carousel.current} className="carousel-dot" selectedClassName="is-selected"></CarouselDots>
          </div>

          <div className="carousel-buttons-container">
            <CarouselButtons
              carousel={carousel.current}
              nextContent={<img src={arrowRight} alt="arrow" />}
              prevContent={<img src={arrowLeft} alt="arrow" />}
              className="carousel-button"
              disableClassName="is-disable"
            ></CarouselButtons>
          </div>
        </div>
      )}

      <div className="com-art__wrapper--desktop">
        {data?.images.map((img, index) => (
          <figure className="com-art__card--desk" key={index}>
            <LazyImage className="image" src={img?.url} alt="img-arts" />
            <LazyImage
              className="download"
              src={iconDownload}
              alt="icon-download"
              onClick={() => {
                downloadImg(img?.url);
              }}
            />
          </figure>
        ))}
      </div>
    </div>
  );
};

export default Art;
