import "./News.scss";
import React, { useState, useEffect } from "react";
import { useLocalize } from "store/actions/localizeAction";
import StyledButton from "components/styled-button/StyledButton";
import { SNS_API, URL_IMAGE } from "utils/constants";
import { axios } from "api";
import { useScreenWidth } from "store/actions/systemAction";
import { chunk } from "lodash-es";
import NewsCard from "./news-card/NewsCard";

export interface NewsFeedDataType {
  images: string[];
  description: string;
  link: string;
  videos: string[];
  date: string;
}

export interface NewsDataType {
  title: string;
  description: string;
  facebookPosts: NewsFeedDataType[];
}

export interface NewsProps {}

const News: React.FC<NewsProps> = (props) => {
  const data = useLocalize<NewsDataType>("home.news");
  const [feed, setFeed] = useState<NewsFeedDataType[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const width = useScreenWidth();
  const MOBILE_BREAK_POINT = 620;
  const TABLET_BREAK_POINT = 1024;
  const FEED_PER_LOAD = 6;
  const INITIAL_FEED = 6;

  useEffect(() => {
    setLoading(true);
    axios.get(SNS_API + "&limit=" + INITIAL_FEED).then((res) => {
      setFeed(res.data);
      setLoading(false);
    });
  }, []);

  const handleSeeMore = () => {
    setLoading(true);
    axios.get(SNS_API + "&limit=" + (feed.length + FEED_PER_LOAD)).then((res) => {
      setFeed(res.data);
      setLoading(false);
    });
  };

  const generateColumn = () => {
    const chunkSize = width < MOBILE_BREAK_POINT ? 1 : width < TABLET_BREAK_POINT ? 2 : 3;
    const tempColumn: JSX.Element[][] = [];

    feed &&
      chunk(feed, chunkSize).forEach((chunk, chunkIndex) => {
        chunk.forEach((post, index) => {
          if (!tempColumn[index]) tempColumn[index] = [];
          tempColumn[index].push(<NewsCard {...post} key={index + chunkIndex} />);
        });
      });

    return tempColumn.map((column, index) => {
      return (
        <div className="com-news__column" key={index}>
          {column}
        </div>
      );
    });
  };

  return (
    <div className="com-news">
      <div className="com-news__title">{data?.title || ""}</div>
      <div className="com-news__description">{data?.description || ""}</div>
      <a href="https://www.facebook.com/Survive-Together-115971459853877/" target="_blank" rel="noopener noreferrer">
        <StyledButton className="com-news__follow-us">FOLLOW US</StyledButton>
      </a>
      <div className="com-news__feed-container">{generateColumn()}</div>
      <div className="com-news__overlay" />

      {loading ? (
        <img className="com-news__loading" src={`${URL_IMAGE}/loading.svg`} alt="loading" />
      ) : (
        <StyledButton onClick={handleSeeMore} className="com-news__see-more">
          SEE MORE
        </StyledButton>
      )}
    </div>
  );
};

export default News;
