import { gsap } from "gsap";
import React, { useEffect, useState } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";
import { useLocalize } from "store/actions/localizeAction";
import { ImageType } from "utils/contentType";
import { useOnClickOutside } from "utils/hook";
import "./Header.scss";

export interface HeaderProps {}
export interface HeaderDataType {
  image: { [key: string]: ImageType };
  link: { [key: string]: string };
  iconMenu: ImageType;
  iconClose: ImageType;
  iconLogo: ImageType;
  bgButton: ImageType;
}

const Header: React.FC<HeaderProps> = (props) => {
  const mobileRef = React.createRef<HTMLDivElement>();
  const headerRef = React.createRef<HTMLDivElement>();

  const [animation, setAnimation] = useState<gsap.core.Tween>();
  const location = useLocation();
  const history = useHistory();
  const data: HeaderDataType = useLocalize("home.header");

  useEffect(() => {
    if (mobileRef.current && animation === undefined) {
      setAnimation(
        // gsap.to(mobileRef.current, 0.3, {
        //   x: 0,
        //   paused: true,
        // })
        gsap.fromTo(
          mobileRef.current,
          {
            x: "100%",
            paused: true,
          },
          {
            x: 0,
            paused: true,
          }
        )
      );
    }
  }, [mobileRef, animation]);

  const moveMenu = () => {
    if (animation) {
      if (animation.progress() === 1) {
        animation.reverse();
      } else {
        animation.play();
      }
    }
  };

  const scrollTo = (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    e.preventDefault();
    if (location.pathname !== "/") {
      history.push("/");
    }
    let section = e.currentTarget?.getAttribute("data-scroll") || "";
    let elm = document.querySelector(section);
    if (elm) {
      //TODO: 80px is height of header
      const y = elm.getBoundingClientRect().top + window.pageYOffset - 80;
      window.scrollTo({ top: y, behavior: "smooth" });
    } else {
      let timeout = 5000;
      let date = new Date().getTime();
      let id = setInterval(() => {
        if (document.querySelector(section) || new Date().getTime() - date > timeout) {
          clearInterval(id);
          document.querySelector(section)?.scrollIntoView();
          window.scrollBy(0, -80);
        }
      }, 500);
    }
  };

  useOnClickOutside(mobileRef, (e: any) => {
    let isClickOnHeader = headerRef.current?.contains(e.target);

    //TODO: Click outside when menu opening =>  close
    if (!isClickOnHeader && animation?.progress() === 1) {
      animation.reverse();
    }
  });

  return (
    <React.Fragment>
      <div className="com-header" ref={headerRef}>
        <figure className="com-header__menu">
          <img src={data?.iconMenu?.url} alt="icon-menu" onClick={moveMenu} />
        </figure>

        <figure
          className="com-header__logo"
          data-scroll=".com-introduce"
          onClick={(e) => {
            scrollTo(e);
          }}
        >
          <img src={data?.iconLogo?.url} alt="icon-logo" />
        </figure>
        <div className="com-header__btnSignup mobile" data-scroll=".com-signup" onClick={(e) => scrollTo(e)}>
          SIGN UP
        </div>
        <figure className="com-header__menu1 desktop">
          <img src={data?.iconMenu?.url} alt="icon-menu" onClick={moveMenu} style={{ cursor: "pointer" }} />
        </figure>
      </div>

      {/* Slide menu */}
      <div className="com-side-menu" ref={mobileRef}>
        <div className="com-side-menu__header">
          <figure
            className="com-header__logo"
            data-scroll=".com-introduce"
            onClick={(e) => {
              moveMenu();
            }}
          ></figure>
          <figure
            data-scroll=".com-introduce"
            onClick={(e) => {
              moveMenu();
            }}
          >
            <img className="com-side-menu__header--close" src={data?.iconClose?.url} alt="icon-close" />
          </figure>
        </div>
        <div className="com-side-menu__content">
          <ul className="com-side-menu__content--ul">
            <li>
              <Link
                to="#about"
                data-scroll=".com-about"
                onClick={(e) => {
                  moveMenu();
                  scrollTo(e);
                }}
              >
                About
              </Link>
            </li>
            <li>
              <Link
                to="#feature"
                data-scroll=".com-feature"
                onClick={(e) => {
                  moveMenu();
                  scrollTo(e);
                }}
              >
                Features
              </Link>
            </li>
            <li>
              <Link
                to="#allies"
                data-scroll=".com-allies"
                onClick={(e) => {
                  moveMenu();
                  scrollTo(e);
                }}
              >
                Allies
              </Link>
            </li>
            <li>
              <Link
                to="#founder"
                data-scroll=".com-founder-pack"
                onClick={(e) => {
                  moveMenu();
                  scrollTo(e);
                }}
              >
                Founder's packs
              </Link>
            </li>
            <li>
              <Link
                to="#art"
                data-scroll=".com-art"
                onClick={(e) => {
                  moveMenu();
                  scrollTo(e);
                }}
              >
                ART OF SURVIVAL
              </Link>
            </li>
            <li>
              <Link
                to="#signup"
                data-scroll=".com-signup"
                onClick={(e) => {
                  moveMenu();
                  scrollTo(e);
                }}
              >
                sign up
              </Link>
            </li>
            <li>
              <Link
                to="#news"
                data-scroll=".com-news"
                onClick={(e) => {
                  moveMenu();
                  scrollTo(e);
                }}
              >
                FACEBOOK posts
              </Link>
            </li>
          </ul>
        </div>

        <div className="com-side-menu__content--logo">
          <div className="text">FOLLOW US</div>
          <figure className="com-header__menu1">
            <a href={data?.link?.facebook} target="_blank" rel="noopener noreferrer">
              <img className="mg-r15" src={data?.image?.iconFacebook?.url} alt="icon-facebook" />
            </a>
            <a href={data?.link?.discord} target="_blank" rel="noopener noreferrer">
              <img src={data?.image?.iconDiscord?.url} alt="icon-game" />
            </a>
          </figure>
        </div>

        <div
          className="com-header__btnSignup slider"
          data-scroll=".com-signup"
          onClick={(e) => {
            moveMenu();
            scrollTo(e);
          }}
        >
          SIGN UP
        </div>
      </div>
    </React.Fragment>
  );
};

export default Header;
