import Footer from "components/base/footer/Footer";
import Header from "components/base/header/Header";
import LazyImage from "libs/lazy-load/LazyImage";
import React from "react";
import { useSystem } from "store/actions/systemAction";
import { URL_IMAGE } from "utils/constants";
import About from "./about/About";
import Allies from "./allies/Allies";
import Art from "./art/Art";
import Feature from "./feature/Feature";
import FounderPack from "./founder-pack/FounderPack";
import "./Home.scss";
import Introduce from "./introduce/Introduce";
import News from "./news/News";
import Signup from "./signup/Signup";
const backgroundUrl = `${URL_IMAGE}/news-background.jpg`;

const Home: React.FC = () => {
  useSystem();

  return (
    <div className="com-home">
      <Header></Header>
      <Introduce></Introduce>
      <About></About>
      <Feature></Feature>
      <Allies></Allies>
      <FounderPack></FounderPack>
      <Art></Art>
      <div className="shared-background">
        <LazyImage className="bg-image" src={backgroundUrl} alt="bg-image" />
        <Signup></Signup>
        <News></News>
      </div>
      <Footer></Footer>
    </div>
  );
};

export default Home;
