import "./StyledButton.scss";
import React from "react";
import { joinNames } from "utils";
import { URL_IMAGE } from "utils/constants";

export interface StyleButtonProps {
  type?: "button" | "submit" | "reset" | undefined;
}
const bgButton = `${URL_IMAGE}/bg_button_69431810d6.png`;
const StyledButton: React.FC<StyleButtonProps & React.AllHTMLAttributes<HTMLButtonElement>> = ({ className, ...props }) => {
  return (
    <button style={{ background: `url(${bgButton}) no-repeat`, backgroundSize: "cover" }} className={joinNames(["com-styled-button", className])} {...props}>
      {props.children}
    </button>
  );
};

export default StyledButton;
