// this files will export all other untils

import { trim } from "lodash-es";

export const tokenizeString = (view: string, delim: string): string[] => {
  if (!view) return [];

  let result: string[] = view.replace(/\s/g, "").split(delim);

  return result;
};

export const joinNames = (names: Array<string | undefined>) => {
  return trim(names.join(" ").replace(/\s\s+/g, " "));
};

export const detectMimeType = (ext: string | undefined) => {
  const MIME_TYPE: { [id: string]: string } = {
    "3gpp": "video",
    "3gp": "video",
    ts: "video",
    mp4: "video",
    mpeg: "video",
    mpg: "video",
    mov: "video",
    webm: "video",
    flv: "video",
    m4v: "video",
    mng: "video",
    asx: "video",
    asf: "video",
    wmv: "video",
    avi: "video",
    gif: "image",
    jpeg: "image",
    jpg: "image",
    png: "image",
    svg: "image",
    svgz: "image",
    tif: "image",
    tiff: "image",
    wbmp: "image",
    webp: "image",
    ico: "image",
    jng: "image",
    bmp: "image",
  };
  return ext ? MIME_TYPE[ext] : ext;
};

export const clampText = (text: string, maxLength: number, breakPoint: number = 0): string => {
  if (text && text.length >= maxLength) {
    if (breakPoint !== 0) {
      if (breakPoint > window.innerWidth) return text.slice(0, maxLength) + "...";
    } else {
      return text.slice(0, maxLength) + "...";
    }
  }
  return text;
};
