import "./SliderMobile.scss";
import React, { useRef, useState, useCallback } from "react";
import Carousel, { CarouselDots, CarouselButtons } from "libs/carousel/Carousel";
import LazyImage from "libs/lazy-load/LazyImage";
import arrowLeft from "assets/images/allies/arrow-left.png";
import arrowRight from "assets/images/allies/arrow-right.png";
export interface SliderMobileProps {
  data: any[];
}

const SliderMobile: React.FC<SliderMobileProps> = (props) => {
  const carousel = useRef<Carousel>();
  const [, setSlideState] = useState({ totalSlides: 0, selectedSlide: 0 });

  const setCarousel = useCallback((ref?: Carousel) => {
    carousel.current = ref;
    if (carousel.current) {
      setSlideState({ totalSlides: carousel.current.scrollSnapList().length, selectedSlide: carousel.current.selectedScrollSnap() });
    }
  }, []);
  return (
    <div className="com-slider-mobile">
      <Carousel
        carouselRef={(ref) => setCarousel(ref)}
        options={{
          draggableClass: "carousel-draggable",
          draggingClass: "carousel-dragging",
          align: "start",
        }}
      >
        {props.data.map((item: any, index: number) => {
          return (
            <div className="com-allies__card" key={index}>
              <figure>
                <LazyImage className="bg-mobile" src={item.imgMb?.url} alt="card-img" />
                <LazyImage className="bg-desk" src={item.image?.url} alt="card-img" />
              </figure>
              <div className="com-allies__card--content">
                <div className="title">{item.title}</div>
                <div className="content">{item.content}</div>
              </div>
            </div>
          );
        })}
      </Carousel>
      <div className="carousel-dots-container">
        <CarouselDots carousel={carousel.current} className="carousel-dot" selectedClassName="is-selected"></CarouselDots>
      </div>
      <div className="carousel-buttons-container">
        <CarouselButtons
          carousel={carousel.current}
          nextContent={<img src={arrowRight} alt="arrow"></img>}
          prevContent={<img src={arrowLeft} alt="arrow"></img>}
          className="carousel-button"
          disableClassName="is-disable"
        ></CarouselButtons>
      </div>
    </div>
  );
};

export default SliderMobile;
