import LazyImage from "libs/lazy-load/LazyImage";
import { isEmpty } from "lodash-es";
import React from "react";
import { useLocalize } from "store/actions/localizeAction";
import { useScreenWidth } from "store/actions/systemAction";
import { ImageType } from "utils/contentType";
import "./Allies.scss";
import SliderMobile from "./slider-mobile/SliderMobile";
import SliderDesk from "./slider-desk/SliderDesk";
export interface AlliesCardProps {
  title: string;
  content: string;
  image: ImageType;
  imgMb: ImageType;
  key?: number;
}

export interface AlliesProps {}

export interface AlliesDataType {
  title: string;
  description: string;
  background: ImageType;
  cards: AlliesCardProps[];
}
const Allies: React.FC<AlliesProps> = (props) => {
  const data: AlliesDataType = useLocalize("home.allies");
  const width = useScreenWidth();

  let description = data?.description.replace(/&nbsp;/g, " ");
  return (
    <div className="com-allies" id="allies">
      <LazyImage className="bg-image index-0" src={data?.background?.url} alt="bg-image" />
      <div className="bg-image index-0 gradient"></div>
      <div className="com-allies__title">{data?.title}</div>
      <div className="com-allies__description" dangerouslySetInnerHTML={{ __html: description }} />

      {!isEmpty(data) && width >= 1280 && <SliderDesk data={data.cards}></SliderDesk>}
      {!isEmpty(data) && width < 1280 && <SliderMobile data={data.cards}></SliderMobile>}
    </div>
  );
};

export default Allies;
