import "./Introduce.scss";
import React from "react";

import { useLocalize } from "store/actions/localizeAction";
import { ImageType } from "utils/contentType";
export interface IntroduceProps {}
export interface IntroduceDataType {
  iconGGPlay: ImageType;
  iconApple: ImageType;
  iconLogoBig: ImageType;
  video: ImageType;
}
const Introduce: React.FC<IntroduceProps> = (props) => {
  const data: IntroduceDataType = useLocalize("home.introduce");
  return data ? (
    <section className="com-introduce">
      <video loop autoPlay muted>
        <source src={data?.video?.url} type="video/mp4" />
      </video>
      <figure className="absolute-center com-introduce__logo">
        <img src={data?.iconLogoBig?.url} alt="logo-big" />
      </figure>

      <div className="com-introduce__download">
        <div>Try it now</div>
        <figure>
          <img className="mg-r05" src={data?.iconApple?.url} alt="googleplay-download" />
          <img className="mg-l05" src={data?.iconGGPlay?.url} alt="applestore-download" />
        </figure>
      </div>
    </section>
  ) : (
    <div></div>
  );
};

export default Introduce;
