import Footer from "components/base/footer/Footer";
import Header from "components/base/header/Header";
import StaticTextPage, { StaticTextPageDataType } from "components/static-text-page/StaticTextPage";
import React from "react";
import { useLocalize } from "store/actions/localizeAction";
import "./Legal.scss";
import { GET_LEGAL } from "utils/queryData";

export interface LegalProps {}

const Legal: React.FC<LegalProps> = (props) => {
  const data = useLocalize<StaticTextPageDataType>("legal", GET_LEGAL);
  return (
    <div className="com-legal">
      <Header></Header>
      {data && <StaticTextPage data={data} classes={{ root: "com-legal__content", title: "com-legal__title", description: "com-legal__description" }}></StaticTextPage>}
      <Footer></Footer>
    </div>
  );
};

export default Legal;
