import "./Feature.scss";
import React from "react";
import { useLocalize } from "store/actions/localizeAction";
import LazyImage from "libs/lazy-load/LazyImage";
import { ImageType } from "utils/contentType";

export interface FeatureProps {}
export interface FeatureDataType {
  title: string;
  description: string;
  cards: {
    title: string;
    content: string;
    image: ImageType;
  }[];
}

const Feature: React.FC<FeatureProps> = (props) => {
  const data: FeatureDataType = useLocalize("home.futures");
  let description = data?.description.replace(/&nbsp;/g, " ");

  return (
    <div className="com-feature" id="feature">
      <div className="com-feature__title">{data?.title}</div>
      <div className="com-feature__description" dangerouslySetInnerHTML={{ __html: description }} />
      <div className="com-feature__wrapper">
        {data?.cards.map((card, index: number) => {
          return (
            <div className="com-feature__card" key={index}>
              <figure>
                <LazyImage className="com-feature__card--img" src={card.image?.url} alt="card-img" />
              </figure>
              <div className="com-feature__card--title">{card.title}</div>
              <div className="com-feature__card--content">{card.content}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Feature;
