import "react-datepicker/dist/react-datepicker.css";
import "./Signup.scss";
import React, { useState } from "react";
import { useLocalize } from "store/actions/localizeAction";
// import panelMobile from "assets/images/signup/panel.png";
import joinSign from "assets/images/signup/join-sign.png";

import { ReactComponent as Panel } from "assets/images/signup/panel.svg";
import DatePicker from "libs/date-picker/DatePicker";
import { COUNTRIES, SUB_MANAGER_URL, URL_IMAGE } from "utils/constants";
import { useForm } from "react-hook-form";
import { recaptcha3 } from "libs/recaptcha/ReCaptchaV3";
import { Modal } from "libs/modal/Modal";
// import api from "api";
import StyledButton from "components/styled-button/StyledButton";
import LazyImage from "libs/lazy-load/LazyImage";
import api from "api";

export interface SignUpDataType {
  description: string;
  legal: string;
  newsletter: string;
  newsletterGL: string;
}

export interface FormData {
  firstname: string;
  lastname: string;
  email: string;
  country: string;
  birthdate: string;
  newsletter: boolean;
  isPromoted: boolean;
  captcha: string;
}

export interface SignupProps {}
const characterModel = `${URL_IMAGE}/signup-character.png`;
const Signup: React.FC<SignupProps> = (props) => {
  const [date, setDate] = useState<Date | null>(null);
  const data = useLocalize<SignUpDataType>("home.singup");
  const [popup, setPopup] = useState<{ success?: boolean; show: boolean }>({ success: false, show: false });
  const [loading, setLoading] = useState<boolean>(false);
  const { register, handleSubmit, reset } = useForm<FormData>();

  const onSubmit = async (submitData: FormData, e: any) => {
    setLoading(true);

    submitData.captcha = await recaptcha3.execute("SignupForm");

    const result = await api.post(SUB_MANAGER_URL, "newsletter", submitData);

    if (result && result.status === 200) {
      setPopup({ success: true, show: true });
      setLoading(false);
      setDate(null);
      reset();
    } else {
      setPopup({ success: false, show: true });
      setLoading(false);
    }
  };

  return (
    <div className="com-signup" id="signup">
      <LazyImage className="com-signup__join-sign" src={joinSign} alt="" />
      <form className="com-signup__content" onSubmit={handleSubmit(onSubmit)}>
        <Panel className="com-signup__panel desktop" />
        <LazyImage className="com-signup__character desktop" src={characterModel} alt="" />
        <div className="com-signup__description com-signup--span-column" dangerouslySetInnerHTML={{ __html: data?.description.replace(/&nbsp;/g, " ") }}></div>
        <div className="com-signup__input-container com-signup--add-strip">
          <input className="com-signup__input" name="firstName" placeholder="First Name" required ref={register} />
        </div>
        <div className="com-signup__input-container com-signup--add-strip">
          <input className="com-signup__input" name="lastName" placeholder="Last Name" required ref={register} />
        </div>
        <div className="com-signup__input-container com-signup--add-strip com-signup--span-column">
          <input className="com-signup__input" name="email" placeholder="Email" pattern="^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$" required ref={register} />
        </div>
        <div className="com-signup__input-container com-signup--add-strip">
          <DatePicker
            classes={{
              root: "com-signup__select",
              input: "com-signup__input",
            }}
            showPopperArrow={false}
            maxDate={new Date()}
            selected={date}
            showMonthDropdown
            showYearDropdown
            yearDropdownItemNumber={100}
            scrollableYearDropdown
            name="birthdate"
            placeholder="Date of Birth"
            onChange={(date) => setDate(date)}
            required
            ref={register}
          ></DatePicker>
        </div>
        <div className="com-signup__input-container com-signup--add-strip com-signup--add-arrow">
          <select required defaultValue="" className="com-signup__select" name="country" ref={register}>
            <option value="" disabled hidden>
              {"Country"}
            </option>
            {COUNTRIES &&
              COUNTRIES.map((country, index) => {
                return (
                  <option key={index} value={country.value}>
                    {country.name}
                  </option>
                );
              })}
          </select>
        </div>
        <span className="com-signup__legal com-signup--span-column">{data?.legal}</span>
        <label className="com-signup__label com-signup--span-column" htmlFor="newsletter">
          <input className="com-signup__checkbox" id="newsletter" name="newsletter" type="checkbox" ref={register} />
          {data?.newsletter}
        </label>
        <label className="com-signup__label com-signup--span-column" htmlFor="isPromoted">
          <input className="com-signup__checkbox" id="isPromoted" name="isPromoted" type="checkbox" ref={register} />
          {data?.newsletterGL}
        </label>
        {loading ? (
          <StyledButton className="com-signup__loading">
            <img src={`${URL_IMAGE}/loading.svg`} alt="Loading..." />
          </StyledButton>
        ) : (
          <StyledButton className="com-signup__submit" type="submit">
            SIGN UP
          </StyledButton>
        )}
      </form>

      <Modal classes={{ root: "com-signup__signup-message", content: "com-signup__message-content" }} isOpen={popup.show} onBackClick={() => setPopup({ show: false })}>
        <img className="com-signup__close" onClick={() => setPopup({ show: false })} src={`${URL_IMAGE}/icon_close.svg`} alt="Close" />
        <div className="com-signup__message">
          {popup.success ? (
            <div>
              <p className="com-signup__message--title">THANKS FOR SUBSCRIBING!</p>
              <p className="com-signup__message--content">You'll now receive newsletters about the latest updates on the game.</p>
            </div>
          ) : (
            <div>
              <p className="com-signup__message--title">Error</p>
              <p className="com-signup__message--content">Sorry, your request cannot be made this time. Please, try again.</p>
            </div>
          )}
        </div>
        <button className="com-signup__confirm" onClick={() => setPopup({ show: false })}>
          OKAY
        </button>
      </Modal>
    </div>
  );
};

export default Signup;
