import StaticTextPage from "components/static-text-page/StaticTextPage";
import LazyImage from "libs/lazy-load/LazyImage";
import React from "react";
import { useLocalize } from "store/actions/localizeAction";
import "./About.scss";

export interface AboutProps {}

const About: React.FC<AboutProps> = (props) => {
  const data = useLocalize("home.about");
  return (
    <div className="com-about" id="about">
      <LazyImage className="bg-image" src={data?.background?.url} alt="bg-image" />
      <div className="com-about__overlay"></div>
      {data && <StaticTextPage data={data} classes={{ root: "com-about__content", title: "com-about__title", description: "com-about__description" }}></StaticTextPage>}
    </div>
  );
};

export default About;
